import React, { Component } from "react";
import Fab from "@material-ui/core/Fab";
import Icon from "@material-ui/core/Icon";
import { withStyles } from "@material-ui/core/styles";
import { NavLink } from "react-router-dom";

const styles = theme => ({
  button: {
    color: "#64bda4",
    background: "white",
    "&:hover": {
      background: "#e5e5e5"
    }
  }
});

class LoginPage extends Component {
  render() {
    const { classes } = this.props;
    return (
      <div className="App Login">
        <NavLink to="/driver" className="login-item">
          <Fab classes={{ root: classes.button }}>
            <Icon>directions_car</Icon>
          </Fab>
          <div className="login-label">Conducteur</div>
        </NavLink>
        <NavLink to="/passenger" className="login-item">
          <Fab classes={{ root: classes.button }}>
            <Icon>person</Icon>
          </Fab>
          <div className="login-label">Passager</div>
        </NavLink>
        {/*        
        <NavLink to="/test" className="login-item">
          <Fab classes={{ root: classes.button }}>
            <Icon>build</Icon>
          </Fab>
          <div className="login-label">Tests</div>
        </NavLink> 
        */}
      </div>
    );
  }
}

export default withStyles(styles)(LoginPage);
