import React, { Component } from "react";
import Icon from "@material-ui/core/Icon";
import Fab from "@material-ui/core/Fab";
import Rides from "../../data/Rides.json";
import { withStyles } from "@material-ui/core/styles";

const styles = theme => ({
  button: {
    margin: "25px 0 0 0",
    color: "black",
    background: "white",
    "&:hover": {
      background: "#bdddc1"
    },
    "&:focus": {
      background: "white"
    }
  },
  buttonfocused: {
    margin: "25px 0 0 0",
    color: "white",
    background: "#64bda4",
    "&:hover": {
      background: "#64bda4"
    },
    "&:focus": {
      background: "#64bda4"
    }
  }
});

var bool = false;

class MatchingRide extends Component {
  constructor() {
    super();
    this.state = {
      selectedOption: ""
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    if (bool) {
      this.setState({
        selectedOption: ""
      });
      bool = !bool;
    } else {
      console.log(this.state.selectedOption);

      this.setState({
        selectedOption: e.currentTarget.value
      });
      bool = !bool;
    }
  }

  render() {
    const { classes } = this.props;
    return (
      <div className="center">
        {Rides.map(rideDetails => {
          return (
            <div key={rideDetails.id} className="center passenger-item">
              {rideDetails.direction === "Partir de la Sonnaz" ? (
                <div>
                  <Icon className="white-text passenger-item-icon">
                    time_to_leave
                  </Icon>
                  <p className="white-text passenger-item-label-b">
                    Conducteur :
                  </p>
                  <p>
                    <span className="white-text passenger-item-label">
                      {rideDetails.driver}
                    </span>
                  </p>
                  <p className="white-text passenger-item-label-b">
                    En direction de :{" "}
                  </p>

                  <span className="white-text passenger-item-label">
                    {rideDetails.outsideDest}
                  </span>
                  <p className="white-text passenger-item-label-b">Départ :</p>

                  <div className="white-text passenger-item-label">
                    {rideDetails.insideDest}
                  </div>
                </div>
              ) : (
                <div>
                  <Icon className="white-text passenger-item-icon">home</Icon>
                  <p className="white-text passenger-item-label-b">
                    Conducteur :
                  </p>
                  <p>
                    <span className="white-text passenger-item-label">
                      {rideDetails.driver}
                    </span>
                  </p>
                  <p className="white-text passenger-item-label-b">
                    En direction de :{" "}
                  </p>
                  <div className="white-text passenger-item-label">
                    {rideDetails.insideDest}
                  </div>
                  <p className="white-text passenger-item-label-b">Départ :</p>
                  <p>
                    <span className="white-text passenger-item-label">
                      {rideDetails.outsideDest}
                    </span>
                  </p>
                </div>
              )}
              <p className="white-text passenger-item-label-t">
                {" "}
                <span className="white-text passenger-item-label">
                  {rideDetails.day}
                </span>
              </p>{" "}
              <span className="white-text passenger-item-label">
                <Icon className="white-text passenger-item-icon">
                  access_time
                </Icon>{" "}
                {rideDetails.time}
              </span>
              <div>
                {this.state.selectedOption === "selected" ? (
                  <Fab
                    onClick={this.handleClick}
                    title="Déselectionner trajet"
                    value="selected"
                    classes={{ root: classes.buttonfocused }}
                  >
                    <Icon>check</Icon>
                  </Fab>
                ) : (
                  <Fab
                    title="Sélectionner trajet"
                    value="selected"
                    onClick={this.handleClick}
                    classes={{ root: classes.button }}
                  >
                    <Icon>add</Icon>
                  </Fab>
                )}
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default withStyles(styles)(MatchingRide);
