import React, { Component } from "react";
import bus_image from "../assets/bus.png";
import bus_image_focused from "../assets/busselected.png";
import train_image from "../assets/train.png";
import train_image_focused from "../assets/trainselected.png";
import Fade from "react-reveal/Fade";

class OutsideDirection extends Component {
  constructor() {
    super();
    this.state = {
      selectedOption: ""
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    this.setState({
      selectedOption: e.currentTarget.id
    });
    console.log(e.currentTarget.id);
  }

  render() {
    return (
      <div className=" destinations">
        <div className="row">
          <div className="container">
            <Fade>
              <div className="col s6 m4 l4 offset-l2 offset-m2 center">
                {this.state.selectedOption === "belfauxcff" ? (
                  <img
                    id="belfauxcff"
                    src={train_image_focused}
                    alt="gare belfaux cff"
                    className="dest-img"
                  />
                ) : (
                  <img
                    id="belfauxcff"
                    onClick={this.handleClick}
                    src={train_image}
                    alt="gare belfaux cff"
                    className="dest-img"
                  />
                )}

                <h5 className="destination-label">Gare de Belfaux CFF</h5>
              </div>
              <div className="col s6 m4 l4 center">
                {this.state.selectedOption === "belfauxvillage" ? (
                  <img
                    id="belfauxvillage"
                    src={train_image_focused}
                    alt="gare belfaux village"
                    className="dest-img"
                  />
                ) : (
                  <img
                    id="belfauxvillage"
                    onClick={this.handleClick}
                    src={train_image}
                    alt="gare belfaux village"
                    className="dest-img"
                  />
                )}
                <h5 className="destination-label">Gare de Belfaux Village</h5>
              </div>
              <div className="col s6 m4 l4 offset-l2 offset-m2 center">
                {this.state.selectedOption === "pensier" ? (
                  <img
                    id="pensier"
                    src={train_image_focused}
                    alt="gare pensier"
                    className="dest-img"
                  />
                ) : (
                  <img
                    id="pensier"
                    onClick={this.handleClick}
                    src={train_image}
                    alt="gare pensier"
                    className="dest-img"
                  />
                )}

                <h5 className="destination-label">Gare de Pensier</h5>
              </div>
              <div className="col s6 m4 l4 center">
                {this.state.selectedOption === "fribourg" ? (
                  <img
                    id="fribourg"
                    src={bus_image_focused}
                    alt="portes de fribourg"
                    className="dest-img"
                  />
                ) : (
                  <img
                    id="fribourg"
                    onClick={this.handleClick}
                    src={bus_image}
                    alt="portes de fribourg"
                    className="dest-img"
                  />
                )}
                <h5 className="destination-label">Arrêt Portes de Fribourg</h5>
              </div>
            </Fade>
          </div>
        </div>
      </div>
    );
  }
}

export default OutsideDirection;
